:root {
  --primary-clr-bg: hsl(0, 100%, 74%);
  --primary-clr-btn: hsl(154, 59%, 51%);
  --accent-clr: hsl(248, 32%, 49%);
  --neutral-clr-drkblue: hsl(249, 10%, 26%);
  --neutral-clr-grayblue: hsl(249, 10%, 26%);
  --neutral-clr-white: hsl(0, 0%, 100%) ;
  --neutral-clr-drkblue-100: hsla(249, 10%, 26%, .1);
  --neutral-clr-drkblue-200: hsla(249, 10%, 26%, .2);
  --neutral-clr-drkblue-500: hsla(249, 10%, 26%, .5);
  --ff:"Poppins", sans-serif;
  --fs-xxlrg:3rem;
  --fs-xlrg:2.25rem;
  --fs-lrg:1.625rem;
  --fs-body:1rem;
  --fs-med:.875rem;
  --fs-sml:.75rem;
  --fs-xsml:.625rem;
  --fw-bold: 700;
  --fw-semi-bold: 600;
  --fw-med: 500;
  --fw-reg: 400;
}

*, *::before, *::after {
  box-sizing: border-box;
  font-size: 100%;
  margin: 0;
  padding: 0;
}

html:focus-within {
  scroll-behavior: smooth;
}

a, a:visited, a:hover {
  text-decoration: none;
  color: inherit;
}

input,
button,
textarea,
select {
  font: inherit;
}

button {
  border: none;
  color: inherit;
  cursor: pointer;
  position: relative;
  letter-spacing: 0.0625rem;
  line-height: 1.8;
  width: 100%;
  font-weight: var(--fw-semi-bold);
}

body {
  font-family: var(--ff);
  font-size: var(--fs-body);
  line-height: 1.5;
  color: var(--neutral-clr-white);
  font-weight: var(--fw-med);
  background-color: var(--primary-clr-bg);
  background-image: url(/images/bg-intro-mobile.png);
}
@media (min-width: 71.875em) {
  body {
    height: 100vh;
    background-image: url(/images/bg-intro-desktop.png);
  }
}

.container {
  max-width: 69.375rem;
  margin: 0 auto;
  padding: 1.5625rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    width: 100vw;
    height: 100vh;
  }
}

.flex {
  display: flex;
  flex-direction: column;
}
@media (min-width: 71.875em) {
  .flex {
    flex-direction: row;
    gap: 4.375rem;
    align-items: center;
  }
}

.button__primary {
  border-radius: 0.3125rem;
  background-color: var(--primary-clr-btn);
  text-transform: uppercase;
  margin-bottom: 0.625rem;
  padding: 0.875rem 1.625rem;
  border-bottom: 0.3125rem solid var(--neutral-clr-drkblue-200);
  transition: opacity 150ms ease-in-out;
}
.button__primary:hover {
  opacity: 0.7;
}
.button__primary:active {
  opacity: 0.5;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
.sr-only {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.hero {
  text-align: center;
  letter-spacing: 0.0625rem;
  line-height: 1.7;
  padding-top: 6.25rem;
}
@media (min-width: 71.875em) {
  .hero {
    text-align: start;
    padding-top: 0;
  }
}
.hero__title {
  font-size: var(--fs-lrg);
  font-weight: var(--fw-bold);
  line-height: 1.3;
  margin-bottom: 1.875rem;
}
@media (min-width: 40em) {
  .hero__title {
    font-size: var(--fs-xlrg);
  }
}
@media (min-width: 71.875em) {
  .hero__title {
    font-size: var(--fs-xxlrg);
    line-height: 1.1;
  }
}
.hero__text {
  font-size: var(--fs-med);
  margin-bottom: 3.75rem;
}

.signUp__banner {
  position: relative;
  border-radius: 0.625rem;
  background-color: var(--accent-clr);
  margin-bottom: 1.5625rem;
  padding: 1.25rem 1.625rem;
  text-align: center;
  letter-spacing: 0.0625rem;
  line-height: 1.8;
  width: 100%;
  font-weight: var(--fw-semi-bold);
}
.signUp__banner::after {
  content: "";
  position: absolute;
  background-color: var(--neutral-clr-drkblue);
  border-radius: 0.625rem;
  inset: 0 0 -0.5rem 0;
  z-index: -1;
  opacity: 0.2;
}
.signUp__banner > span {
  font-weight: var(--fw-reg);
}
.signUp__form {
  background-color: var(--neutral-clr-white);
  border-radius: 0.875rem;
  padding: 1.5625rem;
  position: relative;
}
.signUp__form::after {
  content: "";
  position: absolute;
  background-color: var(--neutral-clr-drkblue);
  border-radius: 0.625rem;
  inset: 0 0 -0.5rem 0;
  z-index: -1;
  opacity: 0.2;
}
@media (min-width: 71.875em) {
  .signUp__form {
    padding: 2.625rem;
    width: 33.75rem;
  }
}
.signUp__field {
  position: relative;
}
.signUp__field > input {
  width: 100%;
  border: solid 1px var(--neutral-clr-drkblue-100);
  border-radius: 0.3125rem;
  padding: 1.125rem 1.4375rem;
  margin-bottom: 1.125rem;
  font-size: var(--fs-med);
  font-weight: var(--fw-bold);
  color: var(--neutral-clr-drkblue);
  position: relative;
}
.signUp__field > input:focus {
  outline: solid 1px var(--neutral-clr-drkblue-500);
}
@media (min-width: 71.875em) {
  .signUp__field > input {
    margin-bottom: 1.375rem;
  }
}
.signUp__field [aria-invalid=true] {
  border: solid 2px var(--primary-clr-bg);
  color: var(--primary-clr-bg);
  background-repeat: no-repeat;
  background-image: url(/images/icon-error.svg);
  background-position: right 1.5625rem center;
  margin-bottom: 2.8125rem;
}
.signUp__field [aria-invalid=true]:focus {
  outline: dotted 2px var(--primary-clr-bg);
}
.signUp__field #errorMessage {
  color: var(--primary-clr-bg);
  font-size: var(--fs-sml);
  font-weight: var(--fw-semi-med);
  font-style: italic;
  text-align: right;
  line-height: 1.75;
  position: absolute;
  top: 4.1875rem;
  right: 0;
}
.signUp__field > ::placeholder {
  color: var(--neutral-clr-drkblue-500);
}
.signUp__terms {
  color: var(--neutral-clr-drkblue-200);
  font-size: var(--fs-sml);
  font-weight: var(--fw-bold);
  text-align: center;
  line-height: 1.75;
}
.signUp__terms > a {
  color: var(--primary-clr-bg);
}